@font-face {
  font-family: robotoRegular;
  src: url("../fonts/Roboto-Regular.ttf");
}
@font-face {
  font-family: EBGaramond;
  src: url("../fonts/EBGaramond-VariableFont_wght.ttf");
}
* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: Roboto, robotoRegular;
  height: 100%;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
  font-family: EBGaramond, Roboto, robotoRegular;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

small {
  font-size: 80%;
  font-weight: 400;
}

a {
  text-decoration: none;
  color: #5878cc;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #789d42;
  border-radius: 4px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #5b6d40;
}

.container-full {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.full {
  height: 100;
  width: 100%;
}

.bg-green {
  background-color: #d1e6b2;
}

.bg-forest {
  background-image: url("../images/forest.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.bg-blue {
  background-color: #5878cc !important;
  color: #fff;
}

.page-title {
  text-align: center;
  letter-spacing: 5px;
  margin-bottom: 2rem;
  padding-top: 2rem;
}

.subtitle {
  text-align: center;
  letter-spacing: 5px;
  margin: 5rem 0 2rem 0;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.m-0 {
  margin: 0;
}

.alert {
  padding: 1.5rem;
  border-radius: 4px;
}

.alert-danger {
  background-color: #ebb5a9;
}

.alert-success {
  background-color: #d1e6b2;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 0.4rem;
  color: #474c3f;
  font-size: 2.5rem;
  font-weight: bold;
  font-style: italic;
}
.logo img {
  width: 70px;
}

@media (min-width: 576px) {
  .page-title {
    font-size: 4rem;
  }

  .subtitle {
    font-size: 2.5rem;
  }
}
.container {
  width: 98%;
  margin: 0 auto;
}

@media (min-width: 992px) {
  .container {
    width: 80%;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 60%;
  }
}
.btn {
  display: inline-block;
  font-weight: 500;
  color: #fff;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0 16px;
  font-size: 16px;
  min-width: 64px;
  line-height: 36px;
  border-radius: 4px;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.btn:hover {
  text-decoration: none;
}

.btn-login {
  font-size: 1rem;
  padding: 0.375rem 0.75rem;
  font-weight: 400;
  border-radius: 0;
  line-height: 1.5;
  position: relative;
  background-color: rgba(149, 205, 67, 0.8) !important;
  color: #474c3f;
}
.btn-login:hover {
  background-color: #95cd43 !important;
}
.btn-login:before, .btn-login:after {
  position: absolute;
  content: "";
  top: -3px;
  left: -3px;
  width: 0;
  height: 0;
  background: transparent;
  border: 3px solid transparent;
}
.btn-login:hover:before {
  animation: animateBeforeBorders 1s linear forwards;
}
.btn-login:hover:after {
  animation: animateAfterBorders 1s linear forwards;
}
.btn-login:hover:before, .btn-login:hover:after {
  animation-delay: 0.2s;
}

.buttons .btn:not(.btn:nth-child(1)) {
  margin-left: 1rem;
}

.btn-primary {
  border-color: #5878cc;
  background-color: #5878cc;
  color: color-contrast(#5878cc);
}
.btn-primary:hover {
  color: color-contrast(#385cb9);
  background-color: #385cb9;
  border-color: #385cb9;
}

.btn-secondary {
  border-color: #6c757d;
  background-color: #6c757d;
  color: color-contrast(#6c757d);
}
.btn-secondary:hover {
  color: color-contrast(#545b62);
  background-color: #545b62;
  border-color: #545b62;
}

.btn-success {
  border-color: #87b543;
  background-color: #87b543;
  color: color-contrast(#87b543);
}
.btn-success:hover {
  color: color-contrast(#6b9035);
  background-color: #6b9035;
  border-color: #6b9035;
}

.btn-info {
  border-color: #58b5cc;
  background-color: #58b5cc;
  color: color-contrast(#58b5cc);
}
.btn-info:hover {
  color: color-contrast(#389fb9);
  background-color: #389fb9;
  border-color: #389fb9;
}

.btn-warning {
  border-color: #d9cb3d;
  background-color: #d9cb3d;
  color: color-contrast(#d9cb3d);
}
.btn-warning:hover {
  color: color-contrast(#beb025);
  background-color: #beb025;
  border-color: #beb025;
}

.btn-danger {
  border-color: #cc4c2f;
  background-color: #cc4c2f;
  color: color-contrast(#cc4c2f);
}
.btn-danger:hover {
  color: color-contrast(#a33d25);
  background-color: #a33d25;
  border-color: #a33d25;
}

.btn-light {
  border-color: #f8f9fa;
  background-color: #f8f9fa;
  color: color-contrast(#f8f9fa);
}
.btn-light:hover {
  color: color-contrast(#dae0e5);
  background-color: #dae0e5;
  border-color: #dae0e5;
}

.btn-dark {
  border-color: #212529;
  background-color: #212529;
  color: color-contrast(#212529);
}
.btn-dark:hover {
  color: color-contrast(#0a0c0d);
  background-color: #0a0c0d;
  border-color: #0a0c0d;
}

.btn-green {
  border-color: #789d42;
  background-color: #789d42;
  color: color-contrast(#789d42);
}
.btn-green:hover {
  color: color-contrast(#5d7933);
  background-color: #5d7933;
  border-color: #5d7933;
}

@keyframes animateBeforeBorders {
  0% {
    width: 0;
    height: 0;
    border-top-color: #5b6d40;
  }
  50% {
    width: 100%;
    height: 0;
    border-top-color: #5b6d40;
    border-right-color: #5b6d40;
  }
  100% {
    width: 100%;
    height: 100%;
    border-top-color: #5b6d40;
    border-right-color: #5b6d40;
  }
}
@keyframes animateAfterBorders {
  0% {
    width: 0;
    height: 0;
    border-bottom-color: #5b6d40;
  }
  50% {
    width: 0;
    height: 100%;
    border-bottom-color: #5b6d40;
    border-left-color: #5b6d40;
  }
  100% {
    width: 100%;
    height: 100%;
    border-bottom-color: #5b6d40;
    border-left-color: #5b6d40;
  }
}
.row {
  display: flex;
  flex-wrap: wrap;
}
.row .col {
  width: 100%;
}

.grid {
  display: grid;
}
.grid .col-title {
  margin: 0.1rem 0;
  padding: 0.5rem 0.2rem;
  width: 100%;
}

@media (min-width: 576px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
.form-login {
  min-height: 400px;
}
.form-login h1,
.form-login .align-center {
  margin-bottom: 1.5rem;
  text-align: center;
}
.form-login .form-control {
  margin-bottom: 1rem;
  width: 100%;
}
.form-login .form-control > * {
  width: 100%;
}
.form-login .form-control label {
  font-size: 0.8em;
  margin-bottom: 10px;
}
.form-login .form-control small {
  color: #cc4c2f;
  text-align: left !important;
}
.form-login .form-control input {
  font-size: 1rem;
  display: block;
  padding: 0.5em;
  outline: none;
  background-color: #f8f9fa;
  border-radius: 4px;
  border: 1px solid transparent;
}
.form-login .btn {
  width: 100%;
  font-weight: bold;
}
.form-login a {
  color: #474c3f;
  text-align: right;
  display: block;
  width: 100%;
  margin-top: 2em;
}

form textarea {
  overflow-y: scroll;
  height: 100px;
  width: 100%;
  border: 0.15em solid #5b6d40;
  outline: none;
  padding: 0.5em;
}

.form {
  background-color: rgba(222, 226, 230, 0.8);
  box-shadow: 0 0 10px #9b9b9b;
  border-radius: 0.125rem;
  padding: 1rem;
}
.form input[type=range] {
  -webkit-appearance: none;
  margin: 10px 0;
}
.form input[type=range]:focus {
  outline: none;
}
.form input[type=range]::-webkit-slider-runnable-track {
  height: 10px;
  cursor: pointer;
  background: #87b543;
  border-radius: 25px;
  border: 0px solid #000101;
}
.form input[type=range]::-webkit-slider-thumb {
  height: 16px;
  width: 30px;
  border-radius: 7px;
  background: #5b6d40;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -3.6px;
}
.form input[type=range]:focus::-webkit-slider-runnable-track {
  background: #87b543;
}
.form input[type=range]::-moz-range-track {
  height: 10px;
  cursor: pointer;
  background: #87b543;
  border-radius: 25px;
  border: 0px solid #000101;
}
.form input[type=range]::-moz-range-thumb {
  border: 0px solid #000000;
  height: 16px;
  width: 30px;
  border-radius: 7px;
  background: #5b6d40;
  cursor: pointer;
}
.form input[type=range]::-ms-track {
  height: 10px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  border-width: 30px 0;
  color: transparent;
}
.form input[type=range]::-ms-fill-lower {
  background: #87b543;
  border: 0px solid #000101;
  border-radius: 50px;
}
.form input[type=range]::-ms-fill-upper {
  background: #87b543;
  border: 0px solid #000101;
  border-radius: 50px;
}
.form input[type=range]::-ms-thumb {
  border: 0px solid #000000;
  height: 16px;
  width: 30px;
  border-radius: 7px;
  background: #5b6d40;
  cursor: pointer;
}
.form input[type=range]:focus::-ms-fill-lower {
  background: #87b543;
}
.form input[type=range]:focus::-ms-fill-upper {
  background: #87b543;
}

@media (min-width: 330px) {
  .form-login {
    margin: 0.5rem;
    padding: 1.5rem;
  }
}
@media (min-width: 576px) {
  .form-login {
    min-width: 500px;
    padding: 2rem 3rem 2rem 3rem;
  }
}
@media (min-width: 768px) {
  .form-login {
    min-width: 550px;
  }
}
.card {
  border-radius: 0.5rem;
  box-shadow: 0 0 6px #ced4da;
}
.card i:not(.btn i) {
  margin-right: 5px;
}
.card .card-header {
  padding: 1rem;
  padding-top: 1.3rem;
  border-radius: 0.5rem 0.5rem 0 0;
  background-color: #d1e6b2;
}
.card .card-header h6 {
  margin: 0;
  margin-right: 20px;
  letter-spacing: 1.5px;
}
.card .card-body {
  padding: 1rem 1rem 0 1rem;
}
.card .card-footer {
  border-top: 1px solid #e9ecef;
  background-color: #f8f9fa;
  color: #343a40;
  padding: 0.2rem 1rem 0.5rem 1rem;
  text-align: right;
}
.card .card-header,
.card .card-footer {
  flex-wrap: wrap;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.card .card-links {
  font-size: 0.8rem;
}
.card .card-links a {
  margin-right: 10px;
  cursor: pointer;
}
.card .card-links a:hover {
  color: #2c4892;
  text-decoration: underline;
}

@media (min-width: 576px) {
  .card-header,
.card-body,
.card-footer {
    padding-left: 2rem !important;
    padding-right: 2rem !important;
  }
}
nav {
  height: auto;
  width: 100%;
  background: white;
  border-bottom: 1px solid #ccc;
  box-shadow: 0 0 6px #343a40;
  padding: 1.5rem 0.5rem;
}
nav #logo {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin-bottom: 0.2rem;
}
nav #nav-links {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}
nav #nav-links .nav-link {
  margin: 0.3rem 0.5rem 0.3rem 0.5rem;
  cursor: pointer;
  display: block;
  color: #000;
  transition: 300ms color;
}
nav #nav-links .nav-link:hover {
  color: #5b6d40;
}
nav #nav-links .nav-link.active {
  border-bottom: 2px solid #87b543;
}
nav .dropdown .dropbtn {
  border: none;
  outline: none;
  background-color: inherit;
  font-family: inherit;
  font-size: inherit;
  margin: 0;
}
nav .dropdown-content {
  display: none;
  position: absolute;
  background-color: #f8f9fa;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
nav .dropdown-content a {
  float: none;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
  color: inherit;
}
nav .dropdown-content a.active {
  background-color: #d1e6b2;
}
nav .dropdown-content a:hover {
  background-color: #e9ecef;
  cursor: pointer;
}
nav .dropdown:hover .dropdown-content {
  display: block;
}

@media (min-width: 768px) {
  nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
  }
}
@media (max-width: 440px) {
  .dropdown-content {
    right: 10%;
  }
}